'use client';

import { useState, useEffect } from 'react';
import { db } from '../config/firebase';
import { collection, getDocs, addDoc, updateDoc, deleteDoc, doc, query, where } from 'firebase/firestore';
import { v4 as uuidv4 } from 'uuid';
import { FiPlus, FiEdit2, FiTrash2, FiChevronDown, FiChevronRight } from 'react-icons/fi';
import ExpandableCard from '../components/shared/ExpandableCard';

interface Namespace {
  id: string;
  'namespace-name': string;
  'namespace-url': string;
}

interface NamespaceAccount {
  id: string;
  'namespace-id': string;
  'namespace-account-id': string;
  'namespace-account-name': string;
  'namespace-account-url-override': string;
  'namespace-account-header': Array<{
    key: string;
    value: string;
  }>;
}

interface NamespaceMethod {
  id: string;
  'namespace-id': string;
  'namespace-account-method-id': string;
  'namespace-account-method-name': string;
  'namespace-account-method-url-override': string;
  'namespace-account-method-queryParams': Array<{
    key: string;
    value: string;
  }>;
}

export default function NamespaceManagement() {
  const [namespaces, setNamespaces] = useState<Namespace[]>([]);
  const [accounts, setAccounts] = useState<{ [key: string]: NamespaceAccount[] }>({});
  const [methods, setMethods] = useState<{ [key: string]: NamespaceMethod[] }>({});
  const [expandedId, setExpandedId] = useState<string | null>(null);
  const [expandedAccountId, setExpandedAccountId] = useState<string | null>(null);
  const [expandedMethodId, setExpandedMethodId] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    loadNamespaces();
  }, []);

  useEffect(() => {
    if (expandedId) {
      loadAccountsAndMethods(expandedId);
    }
  }, [expandedId]);

  const handleAddNamespace = () => {
    // Implement the logic to add a new namespace
  };

  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold">Namespace Management</h1>
        <button
          onClick={() => handleAddNamespace()}
          className="px-4 py-2 bg-[#ff6b4a] text-white rounded flex items-center gap-2"
        >
          <FiPlus /> Add Namespace
        </button>
      </div>

      <div className="space-y-4">
        {namespaces.map((namespace) => (
          <ExpandableCard
            key={namespace.id}
            title={namespace['namespace-name']}
            subtitle={namespace['namespace-url']}
            isExpanded={expandedId === namespace.id}
            onToggle={() => setExpandedId(expandedId === namespace.id ? null : namespace.id)}
          >
            {/* Card content will go here */}
          </ExpandableCard>
        ))}
      </div>
    </div>
  );
} 